.containerSlider{
  position: relative;
  z-index: 22;
}
.slider {
  width: auto;
  margin: 30px 50px 50px;
}

.slick-slide {
  opacity: 0.3;
  transform: scale(0.8);
}
.slick-slide:hover{
  cursor: grab;
}
.slick-slide:active {
  cursor: grabbing;
}
.slick-active{
opacity: 1;
  transform: scale(1);
}
.slick-prev:before,
.slick-next:before {
  color: black;
}

.slick-dots {
  bottom: -30px;
}


/*Slider Background*/
.backgroundOverlay{
  position: absolute;
  top: 0;
  right: 0;
  background: rgb(3,11,13);
  z-index: 20;
    pointer-events: none;
  background: radial-gradient(circle, rgba(3,11,13,0.5) 0%, rgba(3,11,13,1) 100%);
}
.navigationBar{
    background: transparent !important;
    color: white !important;
    padding: 32px 0 !important;
}
.nav-link{
    font-weight: bold;
    font-variant-caps: all-petite-caps;
    margin: 0 32px;
    color: #ffbb26 !important;
}
.backgroundSlider{
  position: absolute;
  top: 0;
  z-index: 18;
    pointer-events: none;
  right: 0;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: .4s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

/*Animasi*/
@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
